import React from "react"
import { Link } from "gatsby"
import styled from "styled-components"
import { graphql, StaticQuery } from "gatsby"
import Img from "gatsby-image"

const Summary = styled.div`
  background-color: #f5f5f5;
  border: 1px solid #d1b464;
  border-radius: 0.2rem;
  padding: 0.3rem;
  color: black;
  transition: all 0.2s ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  & a {
    text-decoration: none;
    color: black;
  }
  & h5 {
    margin-bottom: 0.3rem;
    margin-top: 0.5rem;
  }

  & h5:hover {
    color: #d1b464;
  }

  & p {
    margin: 0;
    padding: 0;
  }

  & img {
    max-width: 100%;
    max-height: 100%;
  }
`

export const formatDate = dateString => {
  const date = new Date(dateString)
  const newDate = `${date.getMonth() +
    1}/${date.getDate()}/${date.getFullYear()}`
  return newDate
}

export const excerptText = text => {
  const cleanText = text.replace(/<\/?[^>]+(>|$)/g, "")
  if (cleanText.length > 125) {
    return cleanText.slice(0, 125)
  } else {
    return cleanText
  }
}

const ProjectInfo = styled.div`
  display: flex;
  justify-content: space-between;
  font-size: 0.7rem;
  flex-direction: row;
  & p:nth-child(1) {
    font-weight: bold;
  }
  & p:nth-child(2) {
    text-align: right;
  }
`

const LatestProjects = () => {
  return (
    <StaticQuery
      query={graphql`
        query($name: String = "Project") {
          allWordpressPost(
            filter: { categories: { elemMatch: { name: { eq: $name } } } }
            sort: { fields: [date], order: DESC }
            limit: 5
          ) {
            edges {
              node {
                title
                excerpt
                categories {
                  name
                }
                slug
                date
                acf {
                  facebook
                  twitter
                  readingDuration
                }
                featured_media {
                  localFile {
                    childImageSharp {
                      fluid(maxWidth: 1000) {
                        ...GatsbyImageSharpFluid
                      }
                    }
                  }
                }
              }
            }
          }
        }
      `}
      render={data => (
        <>
          {data.allWordpressPost.edges.map(item => (
            <Summary key={item.node.slug}>
              <div>
                {item.node.featured_media && (
                  <>
                    <a
                      href={`${item.node.categories[0].name}/${item.node.slug}`}
                    >
                      <Img
                        fluid={
                          item.node.featured_media.localFile.childImageSharp
                            .fluid
                        }
                      />
                    </a>
                  </>
                )}
                <a href={`${item.node.categories[0].name}/${item.node.slug}`}>
                  <h5 dangerouslySetInnerHTML={{ __html: item.node.title }} />
                </a>
                <p
                  style={{ fontSize: "0.7rem" }}
                  dangerouslySetInnerHTML={{
                    __html:
                      item.node.excerpt.slice(3, 200) +
                      ` <strong><a href=${item.node.categories[0].name +
                        "/" +
                        item.node.slug}>[Read More]</a></strong>`,
                  }}
                />
              </div>
              <ProjectInfo>
                {item.node.acf ? (
                  <p>Reading time: {item.node.acf.readingDuration}</p>
                ) : (
                  <p />
                )}
                <p>{formatDate(item.node.date)}</p>
              </ProjectInfo>
            </Summary>
          ))}
        </>
      )}
    />
  )
}

export default LatestProjects
